<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page,index) of pages" :key="index">
        <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img">
            <img :src="item.imgUrl" class="icon-img-content">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
export default {
  name: 'HomeIcons',
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  },
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .icons >>> .swiper-container
    height :0
    padding-bottom :50%
  .icons
    margin-top :.1rem
    .icon
      height :0
      position :relative
      overflow:hidden
      float: left
      width :25%
      padding-bottom :25%
      .icon-img
        position :absolute
        left :0
        top :0
        right:0
        bottom:.44rem
        box-sizing :border-box
        padding:.1rem
        .icon-img-content
          height :100%
          display :block
          margin:0 auto
      .icon-desc
        position :absolute
        left :0
        right:0
        bottom:0
        line-height :.44rem
        height :.44rem
        color :$darkTextColor
        text-align: center
        ellipsis()
</style>



